<template>
    <div>
        <button @click="OnAdd">Add</button>
        <form @submit.stop.prevent="onSave">
            <b-table ref="mytable" small :items="myProvider" :fields="fields">
                <template v-slot:cell()="{ value, item, field: { key } }">

                    <template v-if="sel[pri] != item[pri]">
                        <template v-if="item[pri] != 0">
                            <template v-if="key == pri">
                                {{ value }}
                            </template>
                            <template v-if="key == 'Role'">
                                {{ value }}
                            </template>
                            <template v-if="key == 'ClaimType'">
                                {{ value }}
                            </template>
                            <template v-if="key == 'ClaimName'">
                                {{ value }}
                            </template>
                            <template v-if="key == 'Act'">
                                <b-form-checkbox :disabled="true" class="my-checkbox" v-model="item[key]" :name="key" value=true
                                    unchecked-value=false>
                                </b-form-checkbox>
                            </template>
                        </template>
                    </template>
                    <template v-if="(sel[pri] == item[pri] || item[pri] == 0)">
                        <template v-if="key == pri">
                            <span>{{ value }}</span>
                        </template>

                            <!-- /*{"data":[{"Id":8,"Role":"Admin","ClaimType":"Xoa","ClaimName":"Ban","Act":true},{"Id":9,"                     -->


                        <template v-if="key == 'Role'">
                            <b-form-input autocomplete="off" v-model="item[key]" :name="key" v-validate="'required'"
                                placeholder="" :class="{ 'ko-hop-le': errors.has(key) }">
                            </b-form-input>
                        </template>
                        <template v-if="key == 'ClaimType'">

                            <v-select v-model="item[key]" v-validate="'required'" :name="key" :reduce="(option) => option.id"
                            :options="
                                ClaimTypes
                            " :has-error="errors.has(key)" :class="{ danger: errors.has(key) }" />


                        </template>
                        <template v-if="key == 'ClaimName'">

                            <v-select v-model="item[key]" v-validate="'required'" :name="key" :reduce="(option) => option.id"
                            :options="
                                ClaimNames
                            " :has-error="errors.has(key)" :class="{ danger: errors.has(key) }" />


                        </template>
                        <template v-if="key == 'Act'">

                            <b-form-checkbox class="my-checkbox" v-model="item[key]" :name="key" value=true
                                unchecked-value=false>
                            </b-form-checkbox>


                        </template>
                    </template>
                </template>
                <template #cell(cmd)="row">
                    <template v-if="row.item[pri] == 0">
                        <b-button variant="outline" class="mr-1" size="sm" @click="OnSave(row.item)">
                            <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
                        </b-button>
                        <b-button variant="outline" class="mr-1" size="sm" @click="OnCancel(row.item)">
                            <b-icon-file-x></b-icon-file-x>
                        </b-button>
                    </template>

                    <template v-else>
                        <b-button variant="outline" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                            @click="OnEdit(row.item)">
                            <b-icon-pencil-square></b-icon-pencil-square>
                        </b-button>
                        <b-button variant="outline" class="mr-1" v-if="sel[pri] != row.item[pri]" size="sm"
                            @click="OnDel(row.item)">
                            <b-icon-trash></b-icon-trash>
                        </b-button>
                        <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm"
                            @click="OnSave(row.item)">
                            <b-icon-hand-thumbs-up></b-icon-hand-thumbs-up>
                        </b-button>
                        <b-button variant="outline" class="mr-1" v-if="sel[pri] == row.item[pri]" size="sm"
                            @click="OnCancel(row.item)">
                            <b-icon-file-x></b-icon-file-x>
                        </b-button>
                    </template>
                </template>
            </b-table>
        </form>
    </div>
</template>
<script>

import VeeValidate from "vee-validate";
import Vue from "vue";
Vue.use(VeeValidate, {
    classes: true,
    fieldsBagName: 'veeFields'
});
import vSelect from "vue-select";
Vue.component("v-select", vSelect);


import TumLum from '@/TumLum';
import fetchClient from '@/fetchClient';


export default {
    props: ["role"],
    components: {
    },
    mounted() {
    },
    data() {
        return {
            pri: "Id",
            sel: {},
            adding:false,
                //this.ClaimTypes=res.data;
                //this.ClaimName=res.data;
            ClaimTypes:[],
            ClaimNames:[],
            url:`/api/RoleClaim4`,
            items: [
                { Id: 1, Ten: "thuc 101" },
                { Id: 2, Ten: "thuc 102" },
                { Id: 3, Ten: "thuc 103" },
                { Id: 4, Ten: "thuc 104" },
            ],
            fields: [
                { key: 'Id', sortable: true, thClass: 'text-center', tdClass: 'align-middle', thStyle: { width: "8%" } },
                //{ key: 'ClaimType', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: "80%" } },
                //{ key: 'Role', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: "15%" } },
                { key: 'ClaimType', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: "30%" } },
                { key: 'ClaimName', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: "30%" } },
                { key: 'Act', sortable: true, thClass: 'text-left', tdClass: 'align-middle', thStyle: { width: "10%" } },
                { key: 'Cmd', sortable: true, thClass: 'text-center', tdClass: 'align-middle text-center', thStyle: { width: "22%" } },
            ]
        }
    },
    mounted(){
    },
    methods: {
        myProvider() {
            let vm = this;
            let url = `${this.url}/GetListByRole?role=${this.role}`;
            return fetchClient.get(url)
                .then(res => {
                    //alert(JSON.stringify(res));
                    /*{"data":[{"Id":8,"Role":"Admin","ClaimType":"Xoa","ClaimName":"Ban","Act":true},{"Id":9,"                    
                    */
                    this.GetComboClaimType();
                    this.GetComboClaimName();
                    if (this.adding == true) {
                        let r = {};
                        r[this.pri] = 0;
                        r["Role"] = this.role;
                        r["ClaimType"] = null;
                        r["ClaimName"] = null;
                        r["Act"] = true;
                        res.data.unshift(r);
                    }
                    return res.data;
                })
        },
        OnAdd() {
            this.adding = true;
            this.$refs.mytable.refresh();
        },
        doCancel(item) {
            for (let key in item) {
                if (item[key] != this.sel[key]) {
                    item[key] = this.sel[key]
                }
            }
        },
        OnCancel(item) {
            if (item[this.pri] == 0) {
                let index = this.items.findIndex(x => x[this.pri] == item[this.pri]);
                this.items.splice(index, 1);
                this.adding = false;
                this.$refs.mytable.refresh();
            } else {
                this.doCancel(item);
                this.sel = {};
            }
        },
        OnEdit(item) {
            this.sel = JSON.parse(JSON.stringify(item));
        },
        OnDel(item) {
            this.Del(item[this.pri]);
        },
        OnSave(item) {
            this.$validator.validateAll().then((result) => {
                if (!result) {
                    this.toast("Không hợp lệ");
                    return;
                }
                let r = JSON.parse(JSON.stringify(item));
                if (r[this.pri] == 0) {
                    this.Add(r);
                } else {
                    this.Update(r);
                }
            });
        },
        toast(msg, variant = null) {
            this.$bvToast.toast(msg, {
                title: `Information`,
                variant: variant,
                solid: true,
            });
        },
        async Add(r) {
            return await fetchClient.post(`${this.url}/Add`, r)
                .then(res => {
                    this.toast(`${res.data.msg}`);
                    this.adding = false;
                    this.$refs.mytable.refresh();
                })
                .catch((err) => {
                    TumLum.XuLyErr(err);
                });
        },
        async Update(r) {
            return await fetchClient.post(`${this.url}/Update`, r)
                .then(res => {
                    //alert(JSON.stringify(res));
                    this.sel = {};
                    this.$refs.mytable.refresh();
                })
                .catch((err) => {
                    TumLum.XuLyErr(err);
                });
        },
        async Del(id) {
            return await fetchClient.get(`${this.url}/Delete?id=${id}`)
                .then(res => {
                    this.$refs.mytable.refresh();
                })
                .catch((err) => {
                    TumLum.XuLyErr(err);
                });
        },
        async getById(id) {
            return await fetchClient.get(`${this.url}/GetById?id=${id}`)
                .then(res => {
                    alert(JSON.stringify(res));

                    //this.sel=res.data
                    //alert(JSON.stringify(res));

                    //{"data":{"Id":1003,"TenHocSinh":"Võ Tòng","NamSinh":"2023-02-26T00:00:00","LopHocId":1029},"statu
                    //this.sel = JSON.parse(JSON.stringify(item));

                    // this.sel2 = res.data;
                    // this.showModal();
                })
                .catch((err) => {
                    TumLum.XuLyErr(err);
                });
        },
        async GetComboClaimType(){
            return await fetchClient.get(`/api/NguoiDung4/GetComboClaimType`)
            .then(res=>{
                //alert(JSON.stringify(res))
                this.ClaimTypes=res.data;

            }).catch(err=>{
                TumLum.XuLyErr(err);

            })
        },
        async GetComboClaimName(){
            return await fetchClient.get(`/api/NguoiDung4/GetComboClaimName`)
            .then(res=>{
                //alert(JSON.stringify(res))
                //{"data":[{"label":"Them","id":"Them"},{"label":"Xoa","id":"Xoa"},{"label":"Sua","id":"Sua"}],"
                //this.ClaimTypes=res.data;
                this.ClaimNames=res.data;

            }).catch(err=>{
                TumLum.XuLyErr(err);
            })
        }



    }
}
</script>
  